  showDetail(barang, pembayaran, titip, keterangan) {
    this.props.dispatch(setDetailBarang(barang));
    this.props.dispatch(setDetailPembayaran(pembayaran));
    this.props.dispatch(setDetailTitip(titip));
    this.props.dispatch(setDetailKeterangan(keterangan));
    this.props.dispatch(showModal());
  }
  updateButton(data) {
    switch (data) {
      case "PENJUALAN":
        this.props.dispatch(getValidasiCustomerPenjualan());
        break;
      case "RETURN PENJUALAN":
        this.props.dispatch(getValidasiCustomerReturnPenjualan());
        break;
      case "PEMBAYARAN PER BON":
        this.props.dispatch(getValidasiCustomerPembayaranPerbon());
        break;
      case "TOLAKAN CUSTOMER":
        this.props.dispatch(getValidasiCustomerTolakPembayaranCustomer());
        break;
      case "TITIP GR CUSTOMER":
        this.props.dispatch(getValidasiCustomerTitipGrCustomer());
        break;
      case "PENJUALAN LM":
        this.props.dispatch(getValidasiCustomerPembayaranLM());
        break;
      case "PEMBELIAN LM":
        this.props.dispatch(getValidasiCustomerPembelianLM());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  kirimvalidasi(data) {
    var raw = JSON.parse(localStorage.getItem("FakturTerpilih_kirim")) || [];
    var hasil = {
      trx_customer: raw,
    };
    this.props.dispatch(progressSend());
    switch (data) {
      case "PENJUALAN":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() =>
            this.props.dispatch(
              getValidasiCustomerPenjualan(
                this.state.kode_customer,
                this.state.tanggal
              )
            )
          )
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .then(() => this.props.dispatch(finishSend()))
          .catch((err) => {
            gagal(
              `Gagal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
            );
            this.props.dispatch(finishSend());
          });
        break;
      case "RETURN PENJUALAN":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => {
            this.props.dispatch(
              getValidasiCustomerReturnPenjualan(
                this.state.kode_customer,
                this.state.tanggal
              )
            );
            this.props.dispatch(finishSend());
          })
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => {
            gagal("Gagal Validasi, Check Koneksi Anda");
            this.props.dispatch(finishSend());
          });
        break;
      case "PEMBAYARAN PER BON":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => {
            this.props.dispatch(
              getValidasiCustomerPembayaranPerbon(
                this.state.kode_customer,
                this.state.tanggal
              )
            );
            this.props.dispatch(finishSend());
          })
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch((err) => {
            gagal(`Gagal Validasi, Detial : ${err?.response?.data}`);
            this.props.dispatch(finishSend());
          });
        break;
      case "TOLAKAN PEMBAYARAN CUSTOMER":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => {
            this.props.dispatch(
              getValidasiCustomerReturnPenjualan(
                this.state.kode_customer,
                this.state.tanggal
              )
            );
            this.props.dispatch(finishSend());
          })
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch((err) => {
            gagal(`Gagal Validasi, Detial : ${err?.response?.data}`);
            this.props.dispatch(finishSend());
          });
        break;
      case "TITIP GR CUSTOMER":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => {
            this.props.dispatch(
              getValidasiCustomerTitipGrCustomer(
                this.state.kode_customer,
                this.state.tanggal
              )
            );
            this.props.dispatch(finishSend());
          })
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch((err) => {
            gagal(
              `Gagal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
            );
            this.props.dispatch(finishSend());
          });
        break;
      case "PENJUALAN LM":
        Axios.put(serverNonMaster + "cst/dc/lm", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPembayaranLM()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "PEMBELIAN LM":
        Axios.put(serverNonMaster + "cst/dc/lm", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => this.props.dispatch(getValidasiCustomerPembelianLM()))
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .catch(() => gagal("Gagal Validasi, Check Koneksi Anda"));
        break;
      case "TRANSFER BALANCE":
        AxiosMasterPut("validate-customer/validate", hasil)
          .then(() => berhasil("Berhasil Validasi"))
          .then(() => this.prevStep())
          .then(() => {
            this.props.dispatch(
              getvalidasiCustomerTranseferBalance(
                this.state.kode_customer,
                this.state.tanggal
              )
            );
            this.props.dispatch(finishSend());
          })
          .then(() => localStorage.removeItem("FakturTerpilih"))
          .then(() => localStorage.removeItem("FakturTerpilih_kirim"))
          .catch((err) => {
            gagal(
              `Gagal Validasi, Check Koneksi Anda, Detial : ${err.response.data}`
            );
            this.props.dispatch(finishSend());
          });
        break;
      default:
        console.log("ERROR");
        this.props.dispatch(finishSend());
        break;
    }
  }

